import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatStepper } from '@angular/material/stepper';
import { HelperService } from 'src/app/shared/services/helper.service';
import { UserService } from '../../user.service';
import {
  SearchCountryField,
  CountryISO,
  PhoneNumberFormat,
} from "ngx-intl-tel-input";
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-staff-create',
  templateUrl: './staff-create.component.html',
  styleUrls: ['./staff-create.component.scss']
})
export class StaffCreateComponent implements OnInit {
  companyList: any[] = [];
  departmentList: any[] = [];
  staffLevelList: any[] = [];
  title = "create staff";

  contactForm: FormGroup = new FormGroup({});
  companyForm: FormGroup = new FormGroup({});
  othersForm: FormGroup = new FormGroup({});
  private namePattern = "[a-zA-Z][a-zA-Z ]+[a-zA-Z]$";
  private emailRegEx =
    /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  separateDialCode = false;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  PhoneNumberFormat = PhoneNumberFormat;
  preferredCountries: CountryISO[] = [
    CountryISO.UnitedStates,
    CountryISO.UnitedKingdom,
  ];
  htmlWorkbookJSON: {};
  selectedFile: File;
  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private helperService: HelperService
  ) {}

  ngOnInit(): void {
    this.contactForm = this.fb.group({
      contact_first_name: [
        "",
        [Validators.required, Validators.pattern(this.namePattern)],
      ],
      contact_last_name: [
        "",
        [Validators.required, Validators.pattern(this.namePattern)],
      ],
      contact_email: [
        "",
        [Validators.required, Validators.pattern(this.emailRegEx)],
      ],
      phone_number: ["", [Validators.required]],
    });
    this.companyForm = this.fb.group({
      company: ["", [Validators.required]],
      staff_level: ["", [Validators.required]],
      staff_id: ["", [Validators.required]],
      department: ["", [Validators.required]],
    });
    this.othersForm = this.fb.group({
      sex: [""],
      date_of_birth: [""],
    });

    this.getDepartmentList();
    this.getCompanyList();
    this.getstaffLevel();
  }

  validateForm(form) {
    this.helperService.validateAllFormFields(form);
  }

  getDepartmentList() {}

  getCompanyList() {}

  getstaffLevel() {}

  onSave(stepper: MatStepper) {
    if (this.contactForm.invalid) {
      this.validateForm(this.contactForm);
      // open step 1
      stepper.selectedIndex = 0;
    } else if (this.companyForm.invalid) {
      this.validateForm(this.companyForm);
      // open step 2
      stepper.selectedIndex = 1;
    } else if (this.othersForm.invalid) {
      this.validateForm(this.othersForm);
      // open step 3
      stepper.selectedIndex = 2;
    } else {
      this.userService.createStaff({
        ...this.contactForm.value,
        ...this.companyForm.value,
        ...this.othersForm.value,
      });
    }
  }

  onFileSelected(event) {
    const files = (event.target as HTMLInputElement).files;
    // this.uploadingExcel()
    // this.ngxService.startBackground('file-upload');
    this.htmlWorkbookJSON = {};
    this.selectedFile = files[0];
    const reader = new FileReader();
    reader.onload = (e: any) => {
      const data = new Uint8Array(e.target.result);
      const workbook = XLSX.read(data, { type: "array" });
      /* DO SOMETHING WITH workbook HERE */
      const first_sheet_name = workbook.SheetNames[0];
      /* Get worksheet */
      const worksheet = workbook.Sheets[first_sheet_name];
      const jsonData = XLSX.utils.sheet_to_json(worksheet, { raw: true });
      this.htmlWorkbookJSON = jsonData;
      console.log(this.htmlWorkbookJSON);
      // this.staffsReady = true;
      // this.stopSpinner()
    };
    reader.readAsArrayBuffer(this.selectedFile);
  }

}
