import { Component, OnInit } from "@angular/core";
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from "@angular/animations";
import { UserService } from "../../user.service";
import { HelperService } from "src/app/shared/services/helper.service";

@Component({
  selector: "app-staff-list",
  templateUrl: "./staff-list.component.html",
  styleUrls: ["./staff-list.component.scss"],
  animations: [
    trigger("detailExpand", [
      state("collapsed", style({ height: "0px", minHeight: "0" })),
      state("expanded", style({ height: "*" })),
      transition(
        "expanded <=> collapsed",
        animate("225ms cubic-bezier(0.4, 0.0, 0.2, 1)")
      ),
    ]),
  ],
})
export class StaffListComponent implements OnInit {
  tableLoading: boolean = true;
  title = "Staff List";
  dataSource: any[] = [];
  displayedColumns: any[] = [
    "name",
    "department",
    "email",
    "phone_number",
    "balance",
    "staff_level",
    "last_login",
    "action",
  ];
  expandedElement: any;
  length: number;
  pageSize: number;
  pageIndex: number;

  isStaff: boolean;
  isSid: boolean;
  isComp: boolean;
  isVend: boolean;
  isAcct: boolean;

  constructor(
    private userService: UserService,
    private helperService: HelperService
  ) {}

  ngOnInit(): void {
    this.getUserGroup();
    this.getStaffList();
  }

  getUserGroup() {
    const userGroup = this.helperService.getUserGroup();
    switch (userGroup) {
      case "emp":
        this.isStaff = true;
        break;
      case "ven":
        this.isVend = true;
        break;
      case "cmp_adm":
        this.isComp = true;
        break;
      case "cmp_act":
        this.isAcct = true;
        break;
      case "sid":
        this.isSid = true;
        break;

      default:
        break;
    }
  }

  getStaffList() {
    this.userService.getStaffList(this.pageSize, this.pageIndex);
    this.userService.getStaffListUpdate().subscribe((staff) => {
      this.dataSource = staff.results.map((element, index) => {
        return { ...element, position: index + 1 };
      });
      this.length = staff.count;
      this.tableLoading = false;
    });
  }

  pageChange(value: { pageSize: number; pageIndex: number }) {
    this.tableLoading = true;
    this.pageSize = value.pageSize;
    this.pageIndex = value.pageIndex;
    this.getStaffList();
  }

  deactivateUser(user) {
    this.tableLoading = true;
    user.is_active = !user.is_active;
    this.userService.deactivateUser(user).subscribe(() => {
      this.getStaffList();
    });
  }
}
